$primary-500: #2196f3 !default;
$primary-600: #1e88e5 !default;
$primary-700: #1976d2 !default;

$secondary-500: #e91e63 !default;
$secondary-600: #d81b60 !default;
$secondary-700: #c2185b !default;

// Pico
@import 'pico@1.4.4/pico';

// Global Overrides

:root {
	--border-radius: .5rem;
}

// Set background color on theme-container
.theme-container[data-theme='light'],
.theme-container[data-theme='dark'] {
	background-color: var(--background-color);
}

// Light theme
[data-theme='light'],
:root:not([data-theme='dark']) {
	// Secondary colors
	--secondary: #{$secondary-600};
	--secondary-hover: #{$secondary-700};
	--secondary-focus: #{rgba($secondary-600, 0.125)};
	--secondary-inverse: #{$white};
}

// Dark theme
@media only screen and (prefers-color-scheme: dark) {
	:root:not([data-theme='light']),
	[data-theme='dark'] {
		// Secondary colors
		--secondary: #{$secondary-600};
		--secondary-hover: #{$secondary-500};
		--secondary-focus: #{rgba($secondary-500, 0.25)};
		--secondary-inverse: #{$white};
	}
}
